        <p style={{margin: 0, padding: 0, border: 0, fontSize: 'small'}}>1.18. GAMITUP.COM acts as an intermediary in concluding contracts and provides Services set forth in section 1.9 above. GAMITUP.COM does not allow the Seller to provide services identical to those listed in section 1.9 above.</p>
