        <p style={{margin: 0, padding: 0, border: 0, fontSize: 'small'}}>5.4. &nbsp;Custom Product/s are Physical Products which are of a highly custom and technical nature and it is not always practical or possible to publish all detailed specifications for each Custom Product. Custom Product descriptions are provided by the Sellers, and GAMITUP.COM accepts no responsibility for such descriptions. Information, prices, availability and Custom Product descriptions displayed on the Site are guidelines only. GAMITUP.COM takes no responsibility for the accuracy of this information.</p>
