        <p style={{margin: 0, padding: 0, border: 0, fontSize: 'small'}}>6.11. &nbsp;GAMITUP.COM shall provide the Users and the Sellers with technical support in case of encountering problems with the functionality of the Site or Account.&nbsp;</p>
