        <p style={{margin: 0, padding: 0, border: 0, fontSize: 'small', color: 'rgb(51, 51, 51)'}}>h) &nbsp;To assure the quality of service and to prevent from abusing of Money Back Guarantee program, GAMITUP.COM reserves the right at any time to:</p>
