        <p style={{margin: 0, padding: 0, border: 0, fontSize: 'small', color: 'rgb(51, 51, 51)'}}>i) &nbsp;The refund shall be processed by the Seller or Selling User within 5 working days from the date of receipt of all requested information, including requested documents.</p>
