        <p style={{margin: 0, padding: 0, border: 0, fontSize: 13}}><span style={{margin: 0, padding: 0, border: 0, fontSize: 13, color: 'rgb(51, 51, 51)'}}>j) At its absolute discretion, GAMITUP.COM is entitled to verify and edit the comment provided by the User, Seller or Selling User within the GAMITUP Marketplace Rating System if:</span></p>
