        <p style={{margin: 0, padding: 0, border: 0, fontSize: 16}}><span style={{margin: 0, padding: 0, border: 0, fontSize: 'small', color: 'rgb(51, 51, 51)', fontFamily: 'arial, helvetica, sans-serif'}}><span style={{margin: 0, padding: 0, border: 0, fontSize: 13}}>&nbsp;</span>be genuine, appropriate, complete and lawful;</span></p>
